.navigation {
  width: 100%;
  height: 80px;
  padding-top: 1rem;
  position: fixed;
  top: 0;
  z-index: 99;
  background-color: #1a1a1d; }
  .navigation__logo {
    height: 75%;
    align-self: center; }
  .navigation__toggler {
    display: none; }
  .navigation__menu {
    grid-column: 10 / 13;
    list-style-type: none;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row; }
    .navigation__menu__item {
      font-weight: 300;
      text-transform: uppercase;
      letter-spacing: 3px; }
      .navigation__menu__item:hover {
        color: #c3073f;
        cursor: pointer; }
  .navigation input {
    display: none; }
  @media (max-width: 768px) {
    .navigation {
      position: absolute;
      top: 0; }
      .navigation__menu {
        position: fixed;
        display: none;
        top: 80px;
        left: 0;
        width: 100%;
        height: 100%;
        background: linear-gradient(135deg, #4e4e50 10.12%, #1a1a1d 100%);
        transition: all 0.8s; }
      .navigation__toggler {
        position: relative;
        display: inline-block;
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        flex-direction: row; }
        .navigation__toggler__line {
          position: absolute;
          width: 2rem;
          height: 2.5px;
          background-color: #c3073f;
          transition: 1s all; }
          .navigation__toggler__line:nth-child(1) {
            top: 37%; }
          .navigation__toggler__line:nth-child(2) {
            top: 50%; }
          .navigation__toggler__line:nth-child(3) {
            top: 63%; }
      input:checked ~ .navigation__toggler {
        position: relative; }
        input:checked ~ .navigation__toggler .navigation__toggler__line:nth-child(1) {
          position: absolute;
          top: 50%;
          transform: translateY(-50%) rotateZ(45deg); }
        input:checked ~ .navigation__toggler .navigation__toggler__line:nth-child(2) {
          opacity: 0; }
        input:checked ~ .navigation__toggler .navigation__toggler__line:nth-child(3) {
          position: absolute;
          top: 50%;
          transform: translateY(-50%) rotateZ(-45deg); }
      input:checked ~ .navigation__menu {
        display: block; }
        input:checked ~ .navigation__menu .navigation__menu__item {
          display: block;
          margin: 2rem;
          position: relative; }
          input:checked ~ .navigation__menu .navigation__menu__item:hover {
            color: #f5f5f5; }
          input:checked ~ .navigation__menu .navigation__menu__item:before {
            content: "";
            width: 0%;
            height: 0.5rem;
            background-color: #f5f5f5;
            position: absolute;
            top: 60%;
            left: 0%;
            transition: 0.8s all; }
          input:checked ~ .navigation__menu .navigation__menu__item:hover:before, input:checked ~ .navigation__menu .navigation__menu__item:active:before {
            width: 100%; } }

.hero {
  height: 100%;
  padding-left: 1rem;
  position: relative;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: row; }
  .hero__text__title, .hero__text__subtitle {
    text-transform: uppercase;
    font-weight: 600; }
  .hero__text__title {
    color: #c3073f;
    font-size: 7rem; }
  .hero__text__subtitle {
    color: #708090;
    font-size: 2rem;
    height: 2rem;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: row; }
  @media (max-width: 768px) {
    .hero__text__title {
      font-size: 2.5rem; }
    .hero__text__subtitle {
      font-size: 0.75rem;
      height: 0.75rem; } }
  .hero__device {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    height: fit-content;
    margin-bottom: 0.5rem; }
    @media (max-width: 768px) {
      .hero__device__mouse {
        display: none; }
      .hero__device__mobile {
        display: inline-block; } }
    @media (min-width: 768px) {
      .hero__device__mouse {
        display: inline-block; }
      .hero__device__mobile {
        display: none; } }
    .hero__device__mouse {
      width: 2rem;
      height: 4rem;
      border: 1px solid #f5f5f5;
      border-radius: 5rem;
      align-self: flex-end;
      justify-self: center;
      position: relative; }
      .hero__device__mouse:after {
        content: "";
        width: 0.5rem;
        height: 0.5rem;
        background-color: #f5f5f5;
        border-radius: 50%;
        position: absolute;
        top: 25%;
        left: 50%;
        transform: translateX(-50%);
        animation: mouseAnimation 2s ease-out infinite; }
    .hero__device__mobile {
      width: 2.5rem;
      height: 5rem;
      border: 1px solid #f5f5f5;
      border-radius: 0.5rem;
      position: relative; }
      .hero__device__mobile:before {
        content: "";
        width: 0.6rem;
        height: 0.6rem;
        border: 1px solid #f5f5f5;
        border-radius: 50%;
        position: absolute;
        top: 75%;
        left: 50%;
        transform: translate(-50%, -50%);
        animation: mobileAnimation 2s infinite ease-in; }
      .hero__device__mobile:after {
        content: "";
        width: 0.5rem;
        height: 0.5rem;
        background-color: #f5f5f5;
        border-radius: 50%;
        position: absolute;
        bottom: 0.2rem;
        left: 50%;
        transform: translateX(-50%); }

@keyframes mouseAnimation {
  from {
    top: 25%;
    opacity: 1; }
  to {
    top: 75%;
    opacity: 0; } }

@keyframes mobileAnimation {
  0% {
    opacity: 1;
    background-color: none; }
  25% {
    background-color: none; }
  50% {
    top: 75%;
    background-color: #f5f5f5; }
  85% {
    background-color: #f5f5f5;
    top: 15%;
    opacity: 1; }
  100% {
    background-color: #f5f5f5;
    top: 15%;
    opacity: 0; } }

